$black: #232323;
$gray-line: #f6f6f6;
$orange: #fe5f1e;
$container-width: 1320px;
$duration: 0.15s;

.content {
    &__title {
        margin: 35px 0;
        @media (max-width: 768px) {
            font-size: 36px;
        }
    }

    &__items {
        margin-top: 40px;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 768px) {
            flex-direction: column;
        }
    }
}

.container {
    width: $container-width;
    margin: 0 auto;
    &--cart {
        max-width: 1000px;
        .content__title {
            margin: 0;
        }
    }
}

.cart {
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 40px;
        @media (max-width: 768px) {
            flex-direction: column;

            .content__title {
                margin-bottom: 20px;
            }
        }
    }

    .content__title {
        display: flex;
        align-items: center;
        font-size: 32px;

        .cart__svg {
            position: relative;
            width: 30px;
            height: 30px;
            margin-right: 10px;
            path {
                stroke: $black;
                stroke-width: 1.9;
            }
        }
    }

    &__clear {
        display: flex;
        align-items: center;
        cursor: pointer;

        span {
            display: inline-block;
            margin-left: 7px;
            color: #b6b6b6;
            font-size: 18px;
        }

        span,
        svg,
        path {
            transition: all $duration ease-in-out;
        }

        &:hover {
            svg {
                path {
                    stroke: darken($color: #b6b6b6, $amount: 50);
                }
            }
            span {
                color: darken($color: #b6b6b6, $amount: 50);
            }
        }
    }

    &__item {
        display: flex;
        width: 100%;
        border-top: 1px solid $gray-line;
        padding-top: 30px;
        margin-top: 30px;

        @media (max-width: 768px) {
            flex-direction: column;
            position: relative;
        }

        &-img {
            display: flex;
            align-items: center;
            margin-right: 15px;
            width: 10%;

            @media (max-width: 768px) {
                width: 100%;
                justify-content: center;
                order: 1;
            }

            img {
                width: 80px;
                height: 80px;

                @media (max-width: 768px) {
                    width: 60%;
                    height: 60%;
                }
            }
        }



        @media (max-width: 500px) {
            h3 {
                font-size: 24px;
            }
        }
    }

    &__bottom {
        margin: 50px 0;

        &-details {
            display: flex;
            justify-content: space-between;

            span {
                font-size: 22px;

                &:last-of-type {
                    b {
                        color: $black;
                    }
                }
            }
        }

        &-buttons {
            display: flex;
            justify-content: space-between;
            margin-top: 40px;

            .go-back-btn {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 210px;

                border-color: darken($color: $gray-line, $amount: 10);

                span {
                    color: darken($color: $gray-line, $amount: 20);
                    font-weight: 500;
                    font-weight: 600;
                }

                &:hover {
                    background-color: darken($color: $gray-line, $amount: 90);
                    border-color: darken($color: $gray-line, $amount: 90);

                    span {
                        color: $gray-line;
                    }
                }

                svg {
                    margin-right: 12px;
                    path {
                        fill: transparent;
                        stroke-width: 2;
                    }
                }
            }

            .pay-btn {
                font-size: 16px;
                font-weight: 600;
                width: 210px;
                padding: 16px;
            }

            @media (max-width: 500px) {
                flex-direction: column;

                .button {
                    margin-bottom: 20px;
                    width: 100%;
                    height: 54px;
                }
            }
        }
    }

    &--empty {
        margin: 0 auto;
        width: 560px;
        text-align: center;

        h2 {
            font-size: 32px;
            margin-bottom: 10px;
        }

        p {
            font-size: 18px;
            line-height: 145.4%;
            letter-spacing: 0.01em;
            color: #777777;
        }

        i {
            font-style: normal;
            position: relative;
            top: 2px;
        }

        img {
            display: block;
            width: 300px;
            margin: 45px auto 60px;
        }

        .button--black {
            padding: 12px 0 14px;
            width: 230px;
            margin: 0 auto;
            font-weight: 600;
            font-size: 18px;
        }
    }
}
