.modal{
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.35s;
    z-index: 1;
    margin-right: 5px;

    &__content{
        min-width: 50vw;
        padding: 20px;
        border-radius: 12px;
        background-color: white;
        transform: scale(0.5);
        transition: 0.4s all;
        &.active{
            transform: scale(1);
        }
    }

    &.active{
        opacity: 1;
        pointer-events: all;
    }
}
