.card{
	margin: 10px auto;
	padding: 10px 40px 20px 40px;
	border: 1px solid #f3f3f3;
	border-radius: 40px;
	transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
    a {
        text-decoration: none;
        color: black;
    }

    &:hover {
		box-shadow: 0 20px 35px rgba(0, 0, 0, 0.06);
		transform: translateY(-1px);
	}

	&__img{
		display: block;
		width: 180px;
		height: 180px;
		object-fit: cover;
	}
	&__title{
		margin: 10px 0 10px 0;
		font-size: 17px;
		line-height: 25px;
		font-weight: 500;
		span{
			font-size: 16px;
			font-weight: 400;
		}
	}
	&__price{
		color: #b6b6b6;
		font-size: 18px;
		span{
			font-size: 19px;
			font-weight: 700;
			color: black;
			line-height: 30px;
		}
	}
    &__buy{
        display: flex;
        align-items: center;
        justify-content: space-between;
        &__button{
            min-width: 70px;
            cursor:pointer;
            padding: 5px;
            border-radius: 10px;
            color: black;
        }
    }
}
